<template>
<div class='box'>
  <h3>{{text}}</h3>
  <iframe ref='iframe' :src="iframeSrc"
  style="margin: 0;width: 100%;height: 100%;border: 0px solid #d0d0d0;"
  />
</div>
</template>

<script>
import Hammer from 'hammerjs';

export default {
  mounted () {
    var me = this;
    this.iframeSrc = 'https://beautifulboys.github.io/test/index.html';
    // create a manager for that element
    var mc = new Hammer.Manager(this.$refs.iframe);

    var Pinch = new Hammer.Pinch();
    mc.add(Pinch);

    mc.on('pinch', function (e) {
      console.log(e);
      me.text = '缩放成功';
    });
    //
    // mc.on('doubletap', function (e) {
    //   console.log(e);
    //   me.text = '双击成功';
    // });
  },
  data () {
    return {
      iframeSrc: '',
      text: '232322'
    };
  },
  methods: {

  }
};
</script>

<style lang='less' scoped>
.box {
    width: 100%;
    height: 100%;
    background: #eee;
    box-sizing: border-box;
}
</style>
<style lang="less">

</style>



